.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  background-color: #f5f5dc;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
  align-items: center;
}

.header-logo {
  width: 100px;
  height: 100px;
}

.header-nav-items > ul {
  display: flex;
  font-size: 18px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.header-nav-items > ul > li {
  padding: 10px 15px;
  margin: 5px;
  align-self: center;
  border-radius: 5px;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.header-nav-items > ul > li:hover {
  background-color: #e0e0e0;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.body {
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px;
  padding: 20px;
  background-color: #f5f5dc; /* Lighter shade of beige */
  animation: fadeIn 1s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.restaurant-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.restaurant-card {
  display: flex;
  flex-direction: column;
  width: 250px;
  margin: 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  background: linear-gradient(to bottom, yellow 50%, orange 50%);
  overflow: hidden;
}

.restaurant-card:hover {
  transform: scale(1.05);
  cursor: pointer;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.2);
}

.restaurant-card-image {
  width: 100%;
  height: 200px;
  border-bottom: 1px solid #ddd;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}

.restaurant-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
}

.restaurant-card:hover .restaurant-image {
  transform: scale(1.1);
}

.restaurant-card-details {
  padding: 10px;
  background-color: #333;
  border-radius: 0 0 10px 10px;
  font-size: 16px;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-grow: 1;
  transition: background-color 0.3s;
  height: auto; /* Adjust height based on content */
}

.restaurant-card-details:hover {
  background-color: #444;
}

.restaurant-card-details > p {
  margin: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

.search {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.search-input {
  width: 300px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px 0 0 5px;
  font-size: 16px;
  outline: none;
}

.search-button {
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-left: none;
  border-radius: 0 5px 5px 0;
  background-color: #666;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.search-button:hover {
  background-color: #ffa500; /* Light orange */
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}
